var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-container',[_c('h1',[_vm._v("神奇四侠")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        神奇四侠是一个虚构的超级英雄出现在球队的美国漫画书的出版奇迹漫画。该乐队首次登上The Fantastic Four＃1（1961 年 11月的封面），这有助于在媒体中引领新的现实主义水平。The Fantastic Four是由编辑/合作策划人Stan Lee和艺术家/合作绘画家Jack Kirby创建的第一个超级英雄团队，他们开发了一种创作漫画的协作方法，他们将从此使用这个标题。")]),_c('blockquote',{staticClass:"blockquote"},[_vm._v("        传统上与神奇四侠联系在一起的四个人，在科学使命到外太空之后，他们在接触宇宙射线后获得了超级大国，是科学天才Mister Fantastic（Reed Richards），也是该组织的领导者，他可以将自己的身体伸展到不可思议的长度和形状; 在无形的女孩（苏珊“苏”风暴;后来的“看不见的女人”），谁最终嫁给了里德，谁能够使自己无形的，以后的项目强大的无形力场; 苏的弟弟，可以产生火焰的人类火炬（约翰尼风暴）围绕着他们并飞翔; 和滔天的事（Ben Grimm），他们脾气暴躁但仁慈的朋友，曾经是大学橄榄球明星和Reed的大学室友以及一名优秀的飞行员，由于他的石头般的肉体性质，他拥有巨大的超人力量，耐久性和耐力。")])]),_c('v-img',{staticClass:"stage-img",attrs:{"width":"195","height":"260","src":""}})],1),_c('br'),_c('h2',[_vm._v("创作背景")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('br'),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        自1961年创作以来，神奇四侠被描绘成一个有点功能失调但又充满爱心的家庭。与当时其他漫画书原型打破常规，他们会争吵并保持沉重和琐碎的怨恨，避免匿名或秘密身份，支持名人身份。该团队也因其反复遭遇人物而闻名，例如邪恶的君主Doctor Doom，Kree帝国的无情和残暴的执行者Ronan the Accuser，行星吞噬Galactus，Annihilus，负面区域的统治者，海 -栖息太子Namor，航天银色冲浪者和Skrull战士Kl'rt。")]),_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        杜撰的传说，在1961年，长期杂志和漫画书 出版商 马丁·古德曼是打高尔夫球用两种杰克·利博威茨或欧文·多纳恩费尔德对手公司的DC漫画，当时被称为国家期刊出版物，并且该高管吹嘘与DC的成功新超级英雄团队正义联盟美国。虽然电影制片人和漫画历史学家迈克尔·乌斯兰已经揭穿了这个故事的细节，出版趋势追随者古德曼知道JLA的强劲销售，确实指导了他的漫画编辑Stan Lee，创建一个关于超级英雄团队的漫画系列。根据李在1974年的写作，“马丁提到他已经注意到国家漫画出版的其中一个出版物似乎比大多数出版的更好。它是一本名为The Justice League of America的书，它由一本书组成。超级英雄团队。.